/**
 * BIM Export Page
 * Xut BIM data cho Revit/IFC (Sprint 4)
 */

import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Chip,
  Alert,
  Tabs,
  Tab,
} from '@mui/material';
import {
  Architecture as BIMIcon,
  Inventory as InventoryIcon,
  Build as BuildIcon,
  Code as CodeIcon,
} from '@mui/icons-material';
import BIMExport from '../components/BIMExport';
import IFCViewer from '../components/IFCViewer';

const BIMPage = () => {
  const [activeTab, setActiveTab] = useState(0);

  // Sample design data for demo
  const sampleDesignData = {
    tank_name: 'BL-01',
    tank_type: 'sedimentation',
    dimensions: {
      length: 12,
      width: 6,
      depth: 3.5,
      total_depth: 4.2,
      wall_thickness: 0.3,
      bottom_thickness: 0.4,
    },
    hydraulic_results: {
      volume: { total: 252, per_tank: 252 },
      retention_time: 2.5,
      surface_loading: 35,
    },
  };

  const sampleProjectInfo = {
    project_name: 'Trm XLNT Thnh ph XYZ',
    project_code: 'XLNT-2024-001',
    client: 'Ban QLDA H tng',
    location: 'Qun ABC, TP XYZ',
  };

  return (
    <Container maxWidth="xl">
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
        <BIMIcon color="primary" sx={{ fontSize: 40, mr: 2 }} />
        <Box>
          <Typography variant="h4">BIM Export</Typography>
          <Typography variant="body2" color="text.secondary">
            Xut d liu BIM tng thch vi Autodesk Revit thng qua Dynamo hoc pyRevit
          </Typography>
        </Box>
        <Chip label="Sprint 4" color="secondary" sx={{ ml: 'auto' }} />
      </Box>

      <Alert severity="info" sx={{ mb: 3 }}>
        <Typography variant="body2">
          <strong>BIM Bridge</strong> - To cc file JSON data, Dynamo script (.dyn) v pyRevit script (.py) 
           import geometry v parameters vo Revit di dng native objects c th chnh sa.
        </Typography>
      </Alert>

      <Tabs value={activeTab} onChange={(e, v) => setActiveTab(v)} sx={{ mb: 3 }}>
        <Tab label="Export BIM" />
        <Tab label="3D Preview" />
        <Tab label="Hng dn" />
      </Tabs>

      {activeTab === 0 && (
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <BIMExport 
              designData={sampleDesignData}
              projectInfo={sampleProjectInfo}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <Card>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                  <InventoryIcon sx={{ mr: 1, color: 'primary.main' }} />
                  <Typography variant="h6">
                    Cc file BIM c to
                  </Typography>
                </Box>
                
                <Box sx={{ mb: 3 }}>
                  <Typography variant="subtitle2" color="primary">BIM_Data.json</Typography>
                  <Typography variant="body2" color="text.secondary">
                    Cha geometry (vertices, faces), parameters (kch thc, vt liu), 
                    v metadata (project info, element IDs).
                  </Typography>
                </Box>

                <Box sx={{ mb: 3 }}>
                  <Typography variant="subtitle2" color="primary">Dynamo Script (.dyn)</Typography>
                  <Typography variant="body2" color="text.secondary">
                    Visual programming script c JSON v to Family Instances trong Revit.
                    H tr cc loi: Tanks, Pipes, Manholes.
                  </Typography>
                </Box>

                <Box>
                  <Typography variant="subtitle2" color="primary">pyRevit Script (.py)</Typography>
                  <Typography variant="body2" color="text.secondary">
                    Python script s dng Revit API  to i tng. 
                    Ph hp cho automation v batch processing.
                  </Typography>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      )}

      {activeTab === 1 && (
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <IFCViewer />
          </Grid>
        </Grid>
      )}

      {activeTab === 2 && (
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Card>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                  <BuildIcon sx={{ mr: 1, color: 'primary.main' }} />
                  <Typography variant="h6">
                    Ci t Dynamo
                  </Typography>
                </Box>
                <ol>
                  <li>M Revit 2020 tr ln</li>
                  <li>Tab Manage  Dynamo</li>
                  <li>File  Open  Chn file .dyn  ti</li>
                  <li>Cp nht ng dn file JSON trong node "File Path"</li>
                  <li>Click "Run"  to i tng</li>
                </ol>

                <Alert severity="warning" sx={{ mt: 2 }}>
                  Cn ci t package "Data-Shapes" trong Dynamo Package Manager.
                </Alert>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={6}>
            <Card>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                  <CodeIcon sx={{ mr: 1, color: 'success.main' }} />
                  <Typography variant="h6">
                    Ci t pyRevit
                  </Typography>
                </Box>
                <ol>
                  <li>Ti pyRevit t <a href="https://github.com/eirannejad/pyRevit" target="_blank" rel="noreferrer">GitHub</a></li>
                  <li>Ci t theo hng dn</li>
                  <li>Copy file .py vo th mc pyRevit extension</li>
                  <li>Restart Revit</li>
                  <li>Chy script t pyRevit tab</li>
                </ol>

                <Alert severity="info" sx={{ mt: 2 }}>
                  pyRevit h tr Revit 2017-2024. Kim tra version tng thch.
                </Alert>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      )}
    </Container>
  );
};

export default BIMPage;
