/**
 * Settings Page
 * Ci t h thng
 */

import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  TextField,
  Button,
  Switch,
  FormControlLabel,
  Divider,
  Alert,
  Chip,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
} from '@mui/material';
import {
  Settings as SettingsIcon,
  Storage as DatabaseIcon,
  Language as LanguageIcon,
  Palette as ThemeIcon,
  Backup as BackupIcon,
  RestoreFromTrash as RestoreIcon,
  CheckCircle as OKIcon,
  Info as InfoIcon,
} from '@mui/icons-material';
import SystemStatus from '../components/SystemStatus';

const SettingsPage = () => {
  const [settings, setSettings] = useState({
    autoOpenBrowser: true,
    autoSaveVersions: true,
    language: 'vi',
    theme: 'light',
    defaultScale: '1:100',
    outputFolder: './outputs',
  });

  const handleChange = (name, value) => {
    setSettings(prev => ({ ...prev, [name]: value }));
  };

  const handleBackup = () => {
    alert('ang backup database...');
  };

  const handleRestore = () => {
    if (window.confirm('Bn c chc mun restore database? D liu hin ti s b ghi .')) {
      alert('Chn file backup  restore...');
    }
  };

  return (
    <Container maxWidth="lg">
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
        <SettingsIcon color="primary" sx={{ fontSize: 40, mr: 2 }} />
        <Box>
          <Typography variant="h4">Ci t</Typography>
          <Typography variant="body2" color="text.secondary">
            Cu hnh h thng v ty chnh
          </Typography>
        </Box>
      </Box>

      <Grid container spacing={3}>
        {/* System Status */}
        <Grid item xs={12}>
          <SystemStatus />
        </Grid>

        {/* General Settings */}
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                 Ci t Chung
              </Typography>

              <FormControlLabel
                control={
                  <Switch
                    checked={settings.autoOpenBrowser}
                    onChange={(e) => handleChange('autoOpenBrowser', e.target.checked)}
                  />
                }
                label="T ng m trnh duyt khi khi ng"
              />

              <FormControlLabel
                control={
                  <Switch
                    checked={settings.autoSaveVersions}
                    onChange={(e) => handleChange('autoSaveVersions', e.target.checked)}
                  />
                }
                label="T ng lu version mi thit k"
              />

              <Divider sx={{ my: 2 }} />

              <TextField
                fullWidth
                label="Th mc xut file"
                value={settings.outputFolder}
                onChange={(e) => handleChange('outputFolder', e.target.value)}
                margin="normal"
                size="small"
              />

              <TextField
                fullWidth
                label="T l mc nh"
                value={settings.defaultScale}
                onChange={(e) => handleChange('defaultScale', e.target.value)}
                margin="normal"
                size="small"
              />
            </CardContent>
          </Card>
        </Grid>

        {/* Database */}
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                <DatabaseIcon sx={{ mr: 1, verticalAlign: 'middle' }} />
                Database
              </Typography>

              <Alert severity="success" sx={{ mb: 2 }} icon={<OKIcon />}>
                SQLite database ang hot ng bnh thng
              </Alert>

              <List dense>
                <ListItem>
                  <ListItemText 
                    primary="V tr" 
                    secondary="./data/design_data.db" 
                  />
                </ListItem>
                <ListItem>
                  <ListItemText 
                    primary="Kch thc" 
                    secondary="2.5 MB" 
                  />
                </ListItem>
                <ListItem>
                  <ListItemText 
                    primary="S d n" 
                    secondary="15" 
                  />
                </ListItem>
                <ListItem>
                  <ListItemText 
                    primary="S versions" 
                    secondary="48" 
                  />
                </ListItem>
              </List>

              <Divider sx={{ my: 2 }} />

              <Box sx={{ display: 'flex', gap: 1 }}>
                <Button
                  variant="outlined"
                  startIcon={<BackupIcon />}
                  onClick={handleBackup}
                >
                  Backup
                </Button>
                <Button
                  variant="outlined"
                  color="warning"
                  startIcon={<RestoreIcon />}
                  onClick={handleRestore}
                >
                  Restore
                </Button>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/* About */}
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                <InfoIcon sx={{ mr: 1, verticalAlign: 'middle' }} />
                Thng tin ng dng
              </Typography>

              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <List dense>
                    <ListItem>
                      <ListItemText primary="Tn" secondary="HydroDraft Professional" />
                    </ListItem>
                    <ListItem>
                      <ListItemText primary="Phin bn" secondary="2.0.0" />
                    </ListItem>
                    <ListItem>
                      <ListItemText primary="Build" secondary="Sprint 4 - Production" />
                    </ListItem>
                    <ListItem>
                      <ListItemText primary="Nn tng" secondary="Windows x64" />
                    </ListItem>
                  </List>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2" gutterBottom>
                    Tnh nng  trin khai:
                  </Typography>
                  <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                    <Chip size="small" label="Sprint 1 - Offline" color="success" />
                    <Chip size="small" label="Sprint 2 - Traceable" color="success" />
                    <Chip size="small" label="Sprint 3 - CAD Pro" color="success" />
                    <Chip size="small" label="Sprint 4 - BIM" color="success" />
                  </Box>

                  <Typography variant="subtitle2" sx={{ mt: 2 }} gutterBottom>
                    Tiu chun h tr:
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    TCVN 7957:2008  TCVN 33:2006  TCVN 5574:2018  QCVN 14:2008/BTNMT
                  </Typography>
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};

export default SettingsPage;
