/**
 * 3D Viewer Page
 * Trnh xem IFC 3D (Sprint 4)
 */

import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Chip,
  Alert,
  Button,
  TextField,
  Divider,
} from '@mui/material';
import {
  ViewInAr as ViewerIcon,
  CloudUpload as UploadIcon,
  Download as DownloadIcon,
  FolderOpen as FolderOpenIcon,
  Gamepad as GamepadIcon,
  Assessment as AssessmentIcon,
} from '@mui/icons-material';
import IFCViewer from '../components/IFCViewer';

const ViewerPage = () => {
  const [ifcUrl, setIfcUrl] = useState('');

  const sampleModels = [
    { name: 'B lng 01', url: '/models/tank_01.ifc' },
    { name: 'Trm bm', url: '/models/pump_station.ifc' },
    { name: 'H thng ng', url: '/models/pipe_network.ifc' },
  ];

  return (
    <Container maxWidth="xl">
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
        <ViewerIcon color="primary" sx={{ fontSize: 40, mr: 2 }} />
        <Box>
          <Typography variant="h4">Trnh xem 3D</Typography>
          <Typography variant="body2" color="text.secondary">
            Xem m hnh IFC trc tip trong trnh duyt - khng cn ci t phn mm
          </Typography>
        </Box>
        <Chip label="Sprint 4" color="secondary" sx={{ ml: 'auto' }} />
      </Box>

      <Alert severity="info" sx={{ mb: 3 }}>
        <Typography variant="body2">
          <strong>IFC.js Viewer</strong> - H tr nh dng IFC2x3 v IFC4. 
          Cho php xoay, zoom, ct mt ct v xem thng tin chi tit ca tng i tng.
        </Typography>
      </Alert>

      <Grid container spacing={3}>
        {/* Left - Controls */}
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                <FolderOpenIcon sx={{ mr: 1, color: 'primary.main' }} />
                <Typography variant="h6">Ti m hnh</Typography>
              </Box>

              <TextField
                fullWidth
                label="URL file IFC"
                value={ifcUrl}
                onChange={(e) => setIfcUrl(e.target.value)}
                margin="normal"
                size="small"
                placeholder="https://example.com/model.ifc"
              />

              <Button
                fullWidth
                variant="outlined"
                startIcon={<UploadIcon />}
                sx={{ mt: 1 }}
              >
                Upload t my
              </Button>

              <Divider sx={{ my: 2 }} />

              <Typography variant="subtitle2" gutterBottom>
                Mu c sn:
              </Typography>

              {sampleModels.map((model) => (
                <Button
                  key={model.name}
                  fullWidth
                  variant="text"
                  size="small"
                  onClick={() => setIfcUrl(model.url)}
                  sx={{ justifyContent: 'flex-start', mb: 0.5 }}
                >
                  {model.name}
                </Button>
              ))}
            </CardContent>
          </Card>

          <Card sx={{ mt: 2 }}>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                <GamepadIcon sx={{ mr: 1, color: 'secondary.main' }} />
                <Typography variant="h6">iu khin</Typography>
              </Box>
              <Typography variant="body2" paragraph>
                <strong>Chut tri:</strong> Xoay m hnh
              </Typography>
              <Typography variant="body2" paragraph>
                <strong>Chut phi:</strong> Di chuyn
              </Typography>
              <Typography variant="body2" paragraph>
                <strong>Scroll:</strong> Zoom in/out
              </Typography>
              <Typography variant="body2">
                <strong>Click:</strong> Chn i tng
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        {/* Right - Viewer */}
        <Grid item xs={12} md={9}>
          <IFCViewer ifcUrl={ifcUrl} />

          <Card sx={{ mt: 2 }}>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                <AssessmentIcon sx={{ mr: 1, color: 'info.main' }} />
                <Typography variant="h6">Thng tin m hnh</Typography>
              </Box>
              <Grid container spacing={2}>
                <Grid item xs={6} md={3}>
                  <Box sx={{ textAlign: 'center', p: 2, bgcolor: 'grey.100', borderRadius: 1 }}>
                    <Typography variant="h5" color="primary">12</Typography>
                    <Typography variant="body2">i tng</Typography>
                  </Box>
                </Grid>
                <Grid item xs={6} md={3}>
                  <Box sx={{ textAlign: 'center', p: 2, bgcolor: 'grey.100', borderRadius: 1 }}>
                    <Typography variant="h5" color="secondary">3</Typography>
                    <Typography variant="body2">Levels</Typography>
                  </Box>
                </Grid>
                <Grid item xs={6} md={3}>
                  <Box sx={{ textAlign: 'center', p: 2, bgcolor: 'grey.100', borderRadius: 1 }}>
                    <Typography variant="h5" color="success.main">5</Typography>
                    <Typography variant="body2">Materials</Typography>
                  </Box>
                </Grid>
                <Grid item xs={6} md={3}>
                  <Box sx={{ textAlign: 'center', p: 2, bgcolor: 'grey.100', borderRadius: 1 }}>
                    <Typography variant="h5" color="warning.main">2.5MB</Typography>
                    <Typography variant="body2">File size</Typography>
                  </Box>
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};

export default ViewerPage;
